	export default {
	    data() {
	        return {
	            background: ['color1', 'color2', 'color3'],
	            indicatorDots: true,
	            autoplay: stop(),
	            interval: 2000,
	            duration: 500,
				listHeight:10
	        }
	    },
	    methods: {
				getEleHeight(list){
                let info = uni.createSelectorQuery().in(_self).select(list);
        　　　  　info.boundingClientRect(function(data) { //data - 各种参数
        　　　  　    if(data != null){
                        _self.listHeight = data.height;
                    }else{
                        setTimeout(function(){
                            _self.getEleHeight('.swiper-item');
                        },3)
                    }
        　　    }).exec()
            },
			
			beiz:function(){
				uni.showModal({
					content: "暂无备注",
					showCancel: false,
					confirmText: "确定"
				})
			},
			error:function(){
				uni.redirectTo({
					url:'../index/index'
				})
			}
			
	    }
	}
